/**
 * @module store/prices/helpers
 *
 * @version 1.0.3
 */

import { BigNumber } from 'bignumber.js';

/**
 *     
 *
 * @param {array} -  ,   
 * @param {interval} -   ,    
 *
 * @since Version 1.0.3
 */
export const fillByCandles = (candles, interval, endDate) => {
  const res = [];
  if (candles.length > 0) {
    res.push(candles[0]);

    let nextDate = new Date(res[res.length - 1].time.getTime() + interval);
    let i = 1;

    while (nextDate < endDate) {
      const lastCandle = res[res.length - 1];
      if (i < candles.length) {
        //         
        if (nextDate.getTime() === candles[i].time.getTime()) {
          res.push(candles[i]);
          i += 1;
        } else {
          res.push({
            close: lastCandle.close,
            high: lastCandle.close,
            low: lastCandle.close,
            open: lastCandle.close,
            time: nextDate,
          });
        }
      } else {
        //          
        res.push({
          close: lastCandle.close,
          high: lastCandle.close,
          low: lastCandle.close,
          open: lastCandle.close,
          time: nextDate,
        });
      }

      nextDate = new Date(res[res.length - 1].time.getTime() + interval);
    }
  }

  return res;
};

/**
 * 
 *
 * @param {object} candle - ,  
 * @param {number} tradePrice -   
 * @param {Date} tradeTime -   
 */
export const updateCandle = (candle, tradePrice) => ({
  close: tradePrice,
  high: Math.max(candle.high, tradePrice),
  low: Math.min(candle.low, tradePrice),
  open: candle.open,
  time: candle.time,
});

/**
 *    
 *
 * @param {array} -   ,      
 * @param {object} - 
 * @param {number} -   
 *
 * @since Version 1.0.3
 */
export const addTradeToCandles = (candles, trade, interval) => {
  let res = [];
  const tradePrice = trade.price;
  if (candles.length === 0) {
    //            
    res = [{
      close: tradePrice,
      high: tradePrice,
      low: tradePrice,
      open: tradePrice,
      time: trade.createdAt,
    }];
  } else {
    //    
    const lastCandle = candles[candles.length - 1];
    const timeDelta = trade.createdAt.getTime() - lastCandle.time.getTime();
    if (timeDelta <= interval) {
      if (timeDelta >= 0) {
        //     
        res = [...candles.slice(0, -1), updateCandle(lastCandle, tradePrice)];
      }
    } else {
      //      
      let nextTime = new Date(lastCandle.time.getTime() + interval);
      while (nextTime < trade.createdAt) {
        res.push({
          close: lastCandle.close,
          high: lastCandle.close,
          low: lastCandle.close,
          open: lastCandle.close,
          time: nextTime,
        });
        nextTime = new Date(nextTime.getTime() + interval);
      }
      res[res.length - 1].close = tradePrice;
      res[res.length - 1].high = tradePrice;
      res[res.length - 1].low = tradePrice;
      res[res.length - 1].open = tradePrice;
      res = [...candles, ...res];
    }
  }

  return res;
};

/**
 *         ,    
 *
 * @param {array} dataSet -  ,      
 *
 * @since Version 1.0.2
 *
 */
export const makeObjectForChart = (dataSet) => {
  let res = {
    dataSet: [],
    first: {
      price: null,
      priceDiff: null,
      priceDiffInPercent: null,
    },
    last: {
      price: null,
      priceDiff: null,
      priceDiffInPercent: null,
    },
  };

  if (dataSet.length > 1) {
    res = {
      //    
      dataSet,

      //     
      first: {
        price: dataSet[dataSet.length - 1].price,
        time: dataSet[dataSet.length - 1].time,
      },

      //     
      last: {
        price: dataSet[0].price,
        priceDiff: dataSet[0].price - dataSet[dataSet.length - 1].price,
        priceDiffInPercent: dataSet.length > 1 && dataSet[0].price && dataSet[dataSet.length - 1].price
          ? ((dataSet[0].price - dataSet[dataSet.length - 1].price) / dataSet[dataSet.length - 1].price) * 100
          : 0,
        time: dataSet[0].time,
      },
    };
  }

  return res;
};

/**
 *      
 *
 * @private
 *
 * @since Version 1.0.2
 */
export const average = (dataSet) => {
  let sumOfVolumes = BigNumber(0);
  let sumOfPrices = BigNumber(0);

  for (let i = 0; i < dataSet.length; i += 1) {
    sumOfVolumes = sumOfVolumes.plus(dataSet[i].volume);
    sumOfPrices = sumOfPrices.plus(dataSet[i].volume.times(dataSet[i].price));
  }

  return sumOfPrices.div(sumOfVolumes).toNumber();
};

/**
 *      
 *
 * @private
 *
 * @since Version 1.0.2
 */
export const mapToAverage = (dataSet) => {
  // eslint-disable-next-line
  for (const key of Object.keys(dataSet)) {
    // eslint-disable-next-line
    dataSet[key] = average(dataSet[key]);
  }

  return dataSet;
};

/**
 *          
 *
 * @since Version 1.0.4
 */

export const mapCandlesForChart = candles => candles.map(item => ({
  price: item.close,
  time: item.time,
}));


/**
 * Helper:    
 *
 * @param {array} -    
 * @return {array} -  
 *
 * @since Version 1.0.3
 */

export const invertPriceArray = pricesArray => pricesArray.reverse();



